<template>
  <div class="category-edit">
    <h1>{{ id ? '编辑' : '新建' }}管理员</h1>
    <el-form
      label-width="120px"
      :inline="true"
      @submit.native.prevent="save"
    >
      <el-form-item label="名称">
        <el-input v-model="model.userName"></el-input>
      </el-form-item>

      <el-form-item label="密码">
        <el-input
          type='password'
          v-model="model.password"
        ></el-input>
      </el-form-item>

      <el-form-item>
        <el-button
          type="primary"
          native-type="submit"
        >保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'AdminEdit',
  data() {
    return {
      model: {},
    }
  },
  props: {
    // id: String,
    id: {}, // 另一种写法
  },
  created() {
    this.id && this.fetch()
  },
  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`rest/admin_users/${this.id}`, this.model)
      } else {
        // this.$http.post('rest/admin_users', this.model).then()
        await this.$http.post('rest/admin_users', this.model)
      }

      // 跳转到分页列表
      this.$router.push('/admin_users/list')
      this.$message({
        type: 'success',
        message: '保存成功！'
      })
    },
    async fetch() {
      const res = await this.$http.get(`rest/admin_users/${this.id}`)
      this.model = res.data
    },
  },
  components: {

  },
}
</script>

